import React, { Component } from "react";
import "./AdminHeader.css"
import OrderEntry from '../../orderentry/orderentry';
import ADDREGIONS from "../AddRegions/AddRegions";
import ADDMODALITIES from "../AddModalities/AddModalities";
import Vendors from "../AddVendors/AddVendors";
import {
    Button
} from "react-bootstrap";
import { auth } from "../../Login/Auth";
class AdminHeader extends Component {
    handleClick = () => {
        console.log("Buy");
    };
    constructor() {
        super()

        this.state = {
            orderentry: true,
            regions: false,
            vendors: false,
            modalities: false,

        }
    }
    render() {
        return (
            <div>
                <header className="header-wrap">
                    <div className="header-main order_header">
                        <div className="container">
                            <div className="row no-padding">
                                <div className="col-sm-3">
                                    <div className="site-logo">
                                        <a href="/">
                                            <img
                                                src="images/site-logo.png"
                                                alt="site-logo"
                                            />
                                        </a>
                                    </div>
                                </div>
                                <div className="col-sm-9">
                                    <div className="nav-wrap">
                                        <nav className="navbar navbar-default">
                                            <div className="navbar-header">
                                                <button
                                                    type="button"
                                                    className="navbar-toggle collapsed"
                                                    data-toggle="collapse"
                                                    data-target="#bs-example-navbar-collapse-1"
                                                    aria-expanded="false"
                                                >
                                                    <span className="sr-only">
                                                        Toggle navigation
                                                    </span>
                                                    <span className="icon-bar" />
                                                    <span className="icon-bar" />
                                                    <span className="icon-bar" />
                                                </button>
                                            </div>
                                            <div
                                                className="collapse navbar-collapse"
                                                id="bs-example-navbar-collapse-1"
                                            >
                                                <ul className="nav navbar-nav add-btn">
                                                    <li className={this.state.orderentry ? "active" : ''} disabled={this.state.orderentry} onClick={() => this.setState({ orderentry: !this.state.orderentry, regions: false, modalities: false, vendors: false })}   >
                                                        <a >
                                                            Order Entry
                                                       </a>
                                                    </li>
                                                    <li className={this.state.regions ? "active" : ''} disabled={this.state.regions} onClick={() => this.setState({ orderentry: false, regions: !this.state.regions, modalities: false, vendors: false })}>
                                                        <a >
                                                            Add Regions
                                                       </a>
                                                    </li>
                                                    <li className={this.state.modalities ? "active" : ''} disabled={this.state.modalities} onClick={() => this.setState({ orderentry: false, regions: false, modalities: !this.state.modalities, vendors: false })}>
                                                        <a >
                                                            Add Modalities
                                                       </a>
                                                    </li>
                                                    <li className={this.state.vendors ? "active" : ''} disabled={this.state.vendors} onClick={() => this.setState({ orderentry: false, regions: false, modalities: false, vendors: !this.state.vendors })}>
                                                        <a >
                                                            Add Vendors
                                                       </a>
                                                    </li>
                                                    <li className="your-order add-btn">
                                                        <Button
                                                            onClick={() => auth.logout(this.props.history)}
                                                        >
                                                            Logout
                                                        </Button>
                                                    </li>
                                                </ul>
                                            </div>
                                        </nav>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </header>
                {this.state.orderentry ? <OrderEntry /> : null}
                {this.state.regions ? <ADDREGIONS /> : null}
                {this.state.modalities ? <ADDMODALITIES /> : null}
                {this.state.vendors ? <Vendors /> : null}

            </div>
        );
    }
}

export default AdminHeader;