import React, { Component } from "react";
import "./AddRegions.css";
import { API } from "../../../api/index";
import { Formik } from "formik";
import Loader from "react-loader-spinner";

class ADDREGIONS extends Component {
    constructor() {
        super();
        this.state = {
            Regions: [],
            loading: true
        }
    }
    async componentDidMount() {
        try {
            const Regions = await API.Regions.get({ all: true })
            this.setState({ Regions, loading: false })

        }
        catch (err) {

        }
    }
    render() {
        console.log(this.state)
        const initialValues = {
            abbreviation: '',
            name: '',
            country: '',
            active: false
        }
        return (
            <div className="container">
                {this.state.loading ?
                    <div
                        className="loader"
                    >
                        <Loader type="ThreeDots" color="black" height="100" width="100" />
                    </div>
                    : <div className="row tables-row">
                        <div className="col-md-7">
                            <div className="table-wrap">
                                <table className="table">
                                    <tbody>

                                        <tr>
                                            <th>ID</th>
                                            <th>Abbr</th>
                                            <th>Name</th>
                                            <th>Country</th>
                                            <th>Active</th>
                                        </tr>

                                        {
                                            this.state.Regions.length > 0 ?
                                                this.state.Regions.map((region, index) => {
                                                    return <tr key={index}>
                                                        <td>{region.id}</td>
                                                        <td>{region.abbreviation}</td>
                                                        <td>{region.name}</td>
                                                        <td>{region.country}</td>
                                                        {region.active ? <td>{region.active}<span>Activate</span></td> : <td>{region.active}<span className="active">Deactivate</span></td>}

                                                        <td>{}</td>
                                                    </tr>
                                                })
                                                :
                                                <tr colSpan="8" className="text-center">
                                                    No Record Found
                                                </tr>
                                        }
                                    </tbody>

                                </table>
                            </div>
                        </div>
                        <div className="col-md-5">

                            <Formik
                                enableReinitialize={true}
                                initialValues={initialValues}
                                onSubmit={values => {
                                    this.setState({ loading: true })
                                    API.Regions.create({ data: values })
                                        .then(response => {
                                            if (response !== null) {
                                                let Regions = [...this.state.Regions]
                                                Regions.push(response)
                                                this.setState({ Regions, loading: false });
                                            }
                                        })
                                        .catch(error => {
                                            console.log(error);
                                        });
                                }}
                                render={({ values, handleChange, handleSubmit, resetForm }) => (
                                    <div className="form-wrap">
                                        <div className="row">
                                            <div className="form-group col-md-6">
                                                <label htmlFor="inputEmail4">Abbreviaton</label>
                                                <input name="abbreviation" value={values.abbreviation} onChange={handleChange} className="form-control" id="inputEmail4" placeholder="Short Code" />
                                            </div>
                                            <div className="form-group col-md-6">
                                                <label htmlFor="inputPassword4">Name</label>
                                                <input name="name" className="form-control" value={values.name} onChange={handleChange} id="inputPassword4" placeholder="City" />
                                            </div>
                                        </div>
                                        <div className="row country-code">
                                            <div className="form-group col-md-6">
                                                <label htmlFor="inputPassword4">Contry Code</label>
                                                <input name="country" onChange={handleChange} value={values.country} className="form-control" id="inputPassword4" placeholder="US" />
                                            </div>
                                            <div className="form-group col-md-6 mt-10">
                                                <div className="form-check">
                                                    <input className="form-check-input" name="active"
                                                        checked={values.active}
                                                        value={values.active}
                                                        onChange={handleChange}
                                                        type="checkbox" id="gridCheck" />
                                                    <label className="form-check-label" htmlFor="gridCheck">
                                                        Active?
                                        </label>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="button-wrap">
                                                <button className="btn btn-clear" onClick={() => resetForm(initialValues)} type="submit">Clear</button>
                                                <button className="btn  btn-add" disabled={values.abbreviation === "" || values.name === "" || values.country === ""} onClick={handleSubmit} type="submit">Add</button>
                                            </div>
                                        </div>
                                    </div>

                                )}
                            />
                        </div>
                    </div>}
            </div>
        );
    }
}
export default ADDREGIONS;
