const SecretRoute = ({ component: Component, ...rest }) => (
    <Route
        {...rest}
        render={props =>
            auth.loggedIn() === true ? (
                <>
                <Component {...props} />
                </>
            ) : (
                <Redirect to="/login" />
            )
        }
    />
);


