import React, { Component } from "react";
import "../orderentry.css";
import Dropzone from "react-dropzone";
import JSZip from "jszip";
import {
    Modal
} from "react-bootstrap";
export class ADDSTUDY extends Component {
    constructor() {
        super();
        this.addNotes = React.createRef();

        this.onDrop = files => {
            this.setState({ files });
        };
        this.state = {
            files: [],
            Prior: []
        };
    }
    onDropprior = (files, index) => {
        let Prior = [...this.state.Prior];
        const checkindex = Prior.findIndex(e => e.key === index);
        let obj = {
            key: index,
            files: files
        };
        if (checkindex === -1) {
            Prior.push(obj);
            this.setState({ Prior });
        } else {
            Prior[index] = obj;
            this.setState({ Prior });
        }
    };

    files = index => {
        const files = this.state.Prior[index].files.map(file => (
            <li key={file.name}>
                {file.name} - {file.size} bytes
            </li>
        ));
        return files;
    };

    uploadPrior(files, name) {
        let { onUpload, handleClose } = this.props;
        const Note = this.addNotes.current.value;
        if (name.length > 14) {
            let temp = name.split("");
            temp[temp.length - 1] = parseInt(temp[temp.length - 1]) + 1;
            name = temp.join("");
        } else {
            name = name + "-1";
        }

        var zip = new JSZip();

        for (var i = 0; i < files.length; i++) {
            var f = files[i];
            zip.file(f.name, f);
        }
        zip.generateAsync({ type: "blob" }).then(function (content) {
            console.log(content);
            var params = {
                Key: `${name}.zip`,
                ContentType: "application/zip",
                ContentEncoding: "blob",
                Body: content
            };

            onUpload(name, Note, content);
            handleClose();
            // saveAs(content, `${name}.zip`);
        });
    }

    render() {


        return (
            <Modal
                show={this.props.show}
                onHide={this.props.handleClose}
                className="dimensions2"
                dialogClassName={"x-large"}
            >
                <Modal.Header className="mdhead" closeButton>
                    <Modal.Title>Add Study</Modal.Title>
                </Modal.Header>
                <Modal.Body className="mdhead">

                    <div className="row">
                        <div className="col-md-12 pr-0">
                            <label htmlFor="employe">Note</label>

                            <textarea
                                ref={this.addNotes}
                                id="employe"
                                className="form-control"
                                placeholder="Add Notes"
                            />
                        </div>
                    </div>
                    {/* ) : null} */}
                    <Dropzone onDrop={this.onDrop}>
                        {({ getRootProps, getInputProps }) => (
                            <section className="container">
                                <div
                                    {...getRootProps({ className: "dropzone" })}
                                >
                                    <input {...getInputProps()} />
                                    <p>
                                        Drag 'n' drop DICOM here, or click to
                                        select files
                                    </p>
                                </div>
                                {this.state.files.length !== 0 ? (
                                    <div>
                                        <aside>
                                            <h4>Files</h4>
                                            <ul>
                                                {this.state.files.map(
                                                    (file, index) => (
                                                        <li key={index}>
                                                            {index + 1}-{" "}
                                                            {file.name}
                                                        </li>
                                                    )
                                                )
                                                }
                                            </ul>
                                        </aside>

                                    </div>
                                ) : null}
                            </section>
                        )}
                    </Dropzone>
                    <div className="row">
                        <div className="col-md-6 ml-auto d-flex align-items-end justify-content-end">
                            <button
                                type="button"
                                className="btn btn-danger custom_btnDanger"
                                onClick={this.props.handleClose}
                            >
                                Cancel
                            </button>
                        </div>
                        <div className="col-md-6 ml-auto d-flex align-items-end justify-content-end">
                            <button
                                disabled={this.state.files.length === 0}
                                type="button"
                                className="btn btn-primary mdcustom_btn"
                                onClick={() =>
                                    this.uploadPrior(
                                        this.state.files,
                                        this.props.orderKey
                                    )
                                }
                            >
                                Upload Study
                            </button>
                        </div>
                    </div>
                </Modal.Body>
            </Modal>
        );
    }
}
