import React, { Component } from "react";
import InputMask from "react-input-mask";

import "../orderentry.css";
import Select from "react-select";
import { Modal } from "react-bootstrap";
import { ORDERSTATUS } from "./OrderStatus";
const OrderStatus = [
    { value: "no", label: "No", isOptionSelected: true },
    { value: "pending", label: "Pending" },
    { value: "received", label: "Received" },
    { value: "qualityChecked", label: "Quality Checked" },
    {
        value: "physicianNotified",
        label: "Physician Notified"
    },
    { value: "sent", label: "Sent To Customer" }
];
export class ADDNOTIFICATION extends Component {
    constructor(props) {
        super(props);
        this.state = {
            type: "",
            tempStatus: "",
            orderStatus: "",
            documentSent: false,
            changeState: false,
            receivedBy: "",
            data: "",
            error: {
                type: "",
                message: ""
            },
            search: []
        };
        this.note = React.createRef();

        if (!props.data.qualityChecked) {
            OrderStatus.forEach(status => {
                if (
                    status.value === "physicianNotified" ||
                    status.value === "sent"
                ) {
                    status.isDisabled = true;
                }
            });
        }
    }
    selectStatus = e => {
        console.log(e);
        this.setState({ tempStatus: e, changeState: true });
    };
    selectedMethod = e => {
        this.setState(
            {
                type: e.target.value,
                error: {
                    type: "",
                    message: ""
                }
            },
            () => {
                console.log(this.state.type);
            }
        );
    };
    submit = () => {
        let error = {};
        if (!this.state.data && this.state.type !== "Note") {
            error = {
                type: "data",
                message: "Please select valid Type"
            };
            this.setState({ error });
        } else if (
            this.state.documentSent === false &&
            this.state.type !== "Note" &&
            this.props.data.qualityChecked
        ) {
            error = {
                type: "documentSent",
                message: "Required"
            };
            this.setState({ error });
        } else if (
            this.state.type !== "Note" &&
            this.state.receivedBy === "" &&
            this.props.data.qualityChecked
        ) {
            error = {
                type: "receivedBy",
                message: "Please Enter reciever name"
            };
            this.setState({ error });
        } else if (
            this.state.type === "Note" &&
            this.state.orderStatus.value === "no"
        ) {
            error = {
                type: "Note",
                message: "Please Enter Value name"
            };
            this.setState({ error });
        } else if (
            this.state.type === "Note" &&
            this.note.current !== null &&
            (this.note.current.value.length < 9 ||
                this.note.current.value.length > 256)
        ) {
            error = {
                type: "Note",
                message: "Note should be grater then 9 and less then 256 digits"
            };

            this.setState({ error });
        } else {
            let order_Log = {
                OrderId: this.props.data.orderStauesid,
                CreatedById: 2,
                receivedBy: this.state.receivedBy,
                type: this.state.type,
                data:
                    this.state.type === "Note" ? "N/A" : this.state.data,
                note: this.note.current.value,
                documentSent: this.state.documentSent,
                orderStatus: this.state.orderStatus.value
            };
            console.log(order_Log);
            this.props.handleSubmit(order_Log);
        }

        // console.log(this.data.current.value);
        // console.log(this.note.current.value);
    };
    changeType = type => {
        let search = [];
        console.log(type);
        console.log(`physician${type}`);
        if (
            this.state.data !== "" &&
            this.props.data[type] &&
            this.props.data[`physician${type}`]
        ) {
            if (
                this.props.data[type].includes(this.state.data) &&
                this.props.data[`physician${type}`].includes(this.state.data)
            ) {
                search.push(this.props.data[type]);
                search.push(this.props.data[`physician${type}`]);
                this.setState({
                    search
                });
            } else if (this.props.data[type].includes(this.state.data)) {
                search.push(this.props.data[type]);
                this.setState({
                    search
                });
            } else if (
                this.props.data[`physician${type}`].includes(this.state.data)
            ) {
                search.push(this.props.data[`physician${type}`]);
                this.setState({
                    search
                });
            }
        } else {
            this.setState({
                search: []
            });
        }
    };
    handleInputChange = (text, type) => {
        this.setState(
            {
                data: text
            },
            () => {
                this.changeType(type);
            }
        );
    };

    render() {
        console.log(this.state);
        console.log(this.props);

        return (
            <Modal
                show={this.props.show}
                onHide={this.props.handleClose}
                className="dimensions2"
                dialogClassName={"x-large"}
            >
                <Modal.Header className="mdhead" closeButton>
                    <Modal.Title>Add Entry</Modal.Title>
                </Modal.Header>
                <Modal.Body className="mdhead">
                    <form>
                        <div className="row text-left">
                            <div className="col-md-12 pr-0">
                                <label htmlFor="employe">Created By</label>
                                <input
                                    readOnly
                                    className="form-control"
                                    type="text"
                                    name=""
                                    placeholder="Michael Hobbs"
                                />
                            </div>
                            <div className="col-md-12 pr-0">
                                <label htmlFor="employe">
                                    Update Order Status
                                </label>
                                <Select
                                    placeholder=""
                                    className="track"
                                    value={this.state.orderStatus}
                                    //  disabled={values.issued}
                                    name="selectStatus"
                                    options={OrderStatus}
                                    onChange={this.selectStatus}
                                />
                            </div>
                            {this.state.changeState ? (
                                <ORDERSTATUS
                                    show={this.state.changeState}
                                    handleClose={() =>
                                        this.setState({
                                            changeState: false,
                                            error: {
                                                type: "",
                                                message: ""
                                            }
                                        })
                                    }
                                    onSubmit={() => {
                                        this.setState({
                                            orderStatus: this.state.tempStatus,
                                            changeState: false,
                                            error: {
                                                type: "",
                                                message: ""
                                            }
                                        });
                                        // this.props.ChangeStatus(
                                        //     this.state.selectStatus.label
                                        // );
                                        // this.props.handleClose()
                                        // setFieldValue("read", true);
                                        // setFieldValue("checkRead", false);
                                    }}
                                />
                            ) : null}
                            <div className="col-md-12 pr-0">
                                <label htmlFor="employe">Type</label>
                                <select
                                    onChange={this.selectedMethod}
                                    className="form-control"
                                    name="study"
                                    required={this.state.type !== "Note"}
                                >
                                    <option value="None">None</option>
                                    <option value="NOTE">NOTE</option>
                                    <option value="PHONE">PHONE</option>
                                    <option value="FAX">FAX</option>
                                    <option value="EMAIL">EMAIL</option>
                                    <option value="MAIL">MAIL</option>
                                </select>
                                {this.state.error.type !== "" &&
                                    this.state.error.type === "data" ? (
                                        <p className="error-color">
                                            {this.state.error.message}
                                        </p>
                                    ) : null}
                            </div>
                            {this.state.type !== "Note" ? (
                                <div className="col-md-12 pr-0">
                                    <label htmlFor="employe">
                                        {this.state.type
                                            ? this.state.type
                                            : "Phone Number/Fax Number/Email"}
                                    </label>
                                    {this.state.type === "" ? (
                                        <InputMask
                                            mask="***-***-****"
                                            maskChar={null}
                                            disabled
                                            placeholder="xxx-xxx-xxxx"
                                            type="text"
                                            className="form-control"
                                        />
                                    ) : null}
                                    {this.state.type === "EMAIL" ? (
                                        <div>
                                            <input
                                                className="form-control"
                                                type="text"
                                                name=""
                                                value={this.state.data}
                                                onChange={e => {
                                                    this.handleInputChange(
                                                        e.target.value,
                                                        "email"
                                                    );
                                                }}
                                                placeholder=""
                                            />
                                            {this.state.search.map(
                                                (r, index) => {
                                                    return (
                                                        <li
                                                            key={index}
                                                            onClick={() =>
                                                                this.setState({
                                                                    data: r,
                                                                    search: []
                                                                })
                                                            }
                                                        >
                                                            {" "}
                                                            {r}
                                                        </li>
                                                    );
                                                }
                                            )}
                                        </div>
                                    ) : null}
                                    {this.state.type === "PHONE" ? (
                                        <div>
                                            <InputMask
                                                mask="***-***-****"
                                                maskChar={null}
                                                value={this.state.data}
                                                // onChange={e => {
                                                //     console.log(e.target.value);
                                                // }}
                                                onChange={e => {
                                                    this.handleInputChange(
                                                        e.target.value,
                                                        "telephone"
                                                    );
                                                }}
                                                placeholder="xxx-xxx-xxxx"
                                                type="text"
                                                className="form-control"
                                            />
                                            {this.state.search.map(
                                                (r, index) => {
                                                    return (
                                                        <li
                                                            key={index}
                                                            onClick={() =>
                                                                this.setState({
                                                                    data: r,
                                                                    search: []
                                                                })
                                                            }
                                                        >
                                                            {" "}
                                                            {r}
                                                        </li>
                                                    );
                                                }
                                            )}
                                        </div>
                                    ) : null}
                                    {this.state.type === "FAX" ? (
                                        <div>
                                            <InputMask
                                                mask="***-***-****"
                                                maskChar={null}
                                                value={this.state.data}
                                                // onChange={e => {
                                                //     console.log(e.target.value);
                                                // }}
                                                onChange={e => {
                                                    this.handleInputChange(
                                                        e.target.value,
                                                        "fax"
                                                    );
                                                }}
                                                placeholder="xxx-xxx-xxxx"
                                                type="text"
                                                className="form-control"
                                            />

                                            {this.state.search.map(
                                                (r, index) => {
                                                    return (
                                                        <li
                                                            key={index}
                                                            onClick={() =>
                                                                this.setState({
                                                                    data: r,
                                                                    search: []
                                                                })
                                                            }
                                                        >
                                                            {" "}
                                                            {r}
                                                        </li>
                                                    );
                                                }
                                            )}
                                        </div>
                                    ) : null}
                                    {this.state.type === "MAIL" ? (
                                        <div>
                                            <textarea
                                                style={{
                                                    resize: "none",
                                                    overflow: "hidden"
                                                }}
                                                readOnly
                                                className="form-control"
                                                type="text"
                                                //   ref={this.data}
                                                name=""
                                                onChange={e => {
                                                    this.setState({
                                                        data: e.target.value
                                                    });
                                                }}
                                                value={this.state.data}
                                                // value={`Customer Mail Address: ${
                                                //     this.props.data
                                                //         .streetAddress === "" ||
                                                //     this.props.data.city ==
                                                //         "" ||
                                                //     this.props.data.state
                                                //         .label === ""
                                                //         ? "N/A"
                                                //         : this.props.data
                                                //               .streetAddress +
                                                //           this.props.data.city +
                                                //           this.props.data.state
                                                //               .label
                                                // }`}
                                                placeholder=""
                                            />
                                        </div>
                                    ) : null}
                                </div>
                            ) : null}

                            <div className="col-md-12 pr-0">
                                <label htmlFor="employe">Received By</label>
                                <input
                                    className="form-control"
                                    type="text"
                                    name=""
                                    disabled={
                                        this.state.type === "Note" ||
                                        (this.props.data.qualityChecked ===
                                            false &&
                                            this.state.orderStatus.value !==
                                            "qualityChecked")
                                    }
                                    // disabled={
                                    //     this.state.type === "Note" ||
                                    //     this.props.data.qualityChecked === false
                                    // }
                                    onChange={e =>
                                        this.setState({
                                            receivedBy: e.target.value,
                                            error: {
                                                type: "",
                                                message: ""
                                            }
                                        })
                                    }
                                    placeholder=""
                                />
                                {this.state.error.type !== "" &&
                                    this.state.error.type === "receivedBy" ? (
                                        <p className="error-color">
                                            {this.state.error.message}
                                        </p>
                                    ) : null}
                            </div>

                            <div className="col-md-12 pr-0">
                                <div className="form-check text-center">
                                    <input
                                        required={this.state.type !== "Note"}
                                        className="form-check-input"
                                        type="checkbox"
                                        name="issued"
                                        disabled={
                                            this.state.type === "Note" ||
                                            (this.props.data.qualityChecked ===
                                                false &&
                                                this.state.orderStatus.value !==
                                                "qualityChecked")
                                        }
                                        onChange={e => {
                                            let documentSent = !this.state
                                                .documentSent;
                                            this.setState({
                                                documentSent,
                                                error: {
                                                    type: "",
                                                    message: ""
                                                }
                                            });
                                            //       handleChange(e);
                                            //    this.changeOrderStatus("issued", values);
                                        }}
                                    //   checked
                                    />
                                    <label
                                        className="form-check-label"
                                        htmlFor="inlineCheckbox1"
                                    >
                                        Send Documents?
                                    </label>
                                    {this.state.error.type !== "" &&
                                        this.state.error.type === "documentSent" ? (
                                            <p className="error-color">
                                                {this.state.error.message}
                                            </p>
                                        ) : null}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-12 pr-0 text-left">
                                <label htmlFor="employe">Notes</label>

                                <textarea
                                    required={
                                        this.state.type === "Note" ||
                                        this.state.selectStatus === "no"
                                    }
                                    ref={this.note}
                                    maxLength="256"
                                    minLength="9"
                                    id="employe"
                                    className="form-control"
                                    placeholder="Notes"
                                />
                                {this.state.error.type !== "" &&
                                    this.state.error.type === "Note" ? (
                                        <p className="error-color">
                                            {this.state.error.message}
                                        </p>
                                    ) : null}
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-6 ml-auto d-flex align-items-end justify-content-end">
                                <button
                                    type="button"
                                    className="btn btn-danger custom_btnDanger"
                                    onClick={this.props.handleClose}
                                >
                                    Cancel
                                </button>
                            </div>
                            <div className="col-md-6 ml-auto d-flex align-items-end justify-content-end">
                                <button
                                    onClick={this.submit}
                                    //  type="button"
                                    type="button"
                                    className="btn btn-primary mdcustom_btn"
                                >
                                    Submit
                                </button>
                            </div>
                        </div>
                    </form>
                </Modal.Body>
            </Modal>
        );
    }
}
