import React, { Component } from "react";
import "./orderentry.css";
import InputMask from "react-input-mask";
import { API } from "../../api/index";

import {  Formik } from "formik";
import { ADDSTUDY } from "./Modal/AddStudy";
import { ADDNOTIFICATION } from "./Modal/AddNotification";
import { ORDERSTATUS } from "./Modal/OrderStatus";
import Loader from "react-loader-spinner";

import Select from "react-select";

class OrderEntry extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            loaded: false,
            error: "",
            success: false,
            message: "",
            modalities: [],
            Vendors: [],
            States: [],
            Order: {}
        };
    }

    async componentDidMount() {
        try {
            const formatter = (arr) => arr.filter(arr => arr.active === true).map(({ name, id, abbreviation, country }) => ({ id: id, label: name, value: abbreviation ? abbreviation : name, country }))
            const modalities = formatter(await API.Modalities.get({ all: true }))
            const Vendors = formatter(await API.Vendors.get({ all: true }))
            const States = formatter(await API.Regions.get({ all: true }))

            this.setState({
                modalities,
                Vendors,
                States,
                loading: false
            });
            console.log(modalities, Vendors, States);
        } catch (error) {
            console.log(error);
        }

    }

    SubmitOrderLogs = object => {
        console.log(object);
        this.setState({ error: false, success: false, loading: true });

        API.OrderLog.create({ data: object })
            .then(response => {
                console.log(response);

                this.setState({ Order: response, loading: false });
            })
            .catch(error => {
                console.log(error);
            });
    };
    loadData = key => {
        console.log(key);
        this.setState({ error: false, success: false, loading: true });
        if (key && key.length === 14) {
            API.Orders.get({ id: key })
                .then(response => {
                    console.log(response);
                    if (response != null) {
                        response.Modality.value = response.Modality["code"];
                        delete response.Modality.code;
                        response.Modality.label = response.Modality["name"];
                        delete response.Modality.name;

                        response.Vendor.value = response.Vendor["name"];
                        response.Vendor.label = response.Vendor["name"];
                        delete response.Modality.name;
                        console.log(response);

                        this.setState({
                            Order: response,
                            loaded: true,
                            loading: false
                        });
                    } else {
                        this.setState({
                            error: true,
                            message: "Error fetching data",
                            loading: false
                        });
                    }
                })
                .catch(error => {
                    console.log(error);
                    //       this.setState({error: error.message})
                });
        } else {
            this.setState({
                error: true,
                message: "Please Enter Valid Tracking key",
                loading: false
            });
        }
    };
    UploadStudy = (orderId, notes, dicom) => {
        this.setState({ error: false, success: false, loading: true });
        let options = {
            orderId,
            notes,
            dicom
        };
        console.log(options)
        API._uploadStudy(options )
            .then(response => {
                console.log(response)
                let Order = { ...this.state.Order }
                this.setState({ Order, loading: false });
            })
            .catch(error => {
                console.log(error);
                //       this.setState({error: error.message})
            });
    };
    changeOrderStatus = (key, values) => {
        console.log(values);
        let status = {};
        status.id = values.orderStauesid;
        if (key === "issued") {
            status.issued = key === "issued" ? true : values.issued;
        } else if (key === "received") {
            status.received = key === "received" ? true : values.received;
        } else if (key === "pending") {
            status.pending = key === "pending" ? true : values.pending;
        } else if (key === "read") {
            status.read = key === "read" ? true : values.read;
        } else if (key === "reportGenerated") {
            status.reportGenerated = key === "reportGenerated" ? true : values.reportGenerated;
        } else if (key === "inPlainEnglishGenerated") {
            status.inPlainEnglishGenerated = key === "inPlainEnglishGenerated" ? true : values.inPlainEnglishGenerated;
        } else if (key === "qualityChecked") {
            status.qualityChecked = key === "qualityChecked" ? true : values.qualityChecked;
        } else if (key === "physicianNotified") {
            status.physicianNotified = key === "physicianNotified" ? true : values.physicianNotified;
        } else if (values.sent || key === "sent") {
            status.sent = key === "sent" ? true : values.sent;
        }
        console.log(status);

        this.setState({ error: false, success: false, loading: true });
        // UpdateOrderStatus(status)
        API.OrderStatuses.update({ data: status })
            .then(response => {
                console.log(response);
                let Order = { ...this.state.Order };
                Order.OrderStatus = response;
                this.setState({ Order, loading: false });
            })
            .catch(error => {
                console.log(error);
                //       this.setState({error: error.message})
            });
    };

    SendOrder = orderData => {
        const { Order } = this.state;
        this.setState({
            error: false,
            success: false,
            loading: true,
            tracker: null
        });
        let obj = {};
        obj.id = orderData.orderStauesid;
        let state = {}
        if (orderData.state.value !== "") {
            state.id = orderData.state.id
            state.name = orderData.state.value
        }
        console.log(orderData)
        if (JSON.stringify(Order.Vendor) != JSON.stringify(orderData.vendor)) {
            if (orderData.vendor && orderData.vendor.id) {
                obj.Vendor = {
                    id: orderData.vendor.id
                };
            } else {
                obj.Vendor = {};
            }
        }

        if (JSON.stringify(Order.Modality) != JSON.stringify(orderData.modality)) {
            if (orderData.modality && orderData.modality.id) {
                obj.Modality = {
                    id: orderData.modality.id
                };
            } else {
                obj.Modality = {};
            }
        }

        let address = {};

        if (orderData.streetAddress === "" && orderData.city === "" && (orderData.state === {} || orderData.state.value === "")) {
            address = null;
        } else {
            address = {
                addressCountry: orderData.state.country,
                streetAddress: orderData.streetAddress,
                addressLocality: orderData.city,
                postalCode: orderData.postalCode,
                addressRegion: orderData.state.value
            };
        }
        let Customer = {};
        if (JSON.stringify(Customer.PostalAddress) != JSON.stringify(address)) {
            Customer = {
                givenName: orderData.givenName,
                familyName: orderData.familyName,
                email: orderData.email,
                telephone: orderData.telephone,
                faxNumber: orderData.fax,
                notifyByMail: orderData.notifyByMail,
                notifyByEmail: orderData.notifyByEmail,
                notifyByFax: orderData.notifyByFax,
                PostalAddress: address
            };
        } else {
            Customer = {
                givenName: orderData.givenName,
                familyName: orderData.familyName,
                email: orderData.email,
                telephone: orderData.telephone,
                faxNumber: orderData.fax,
                notifyByMail: orderData.notifyByMail,
                notifyByEmail: orderData.notifyByEmail,
                notifyByFax: orderData.notifyByFax,
                PostalAddress: this.state.Customer.PostalAddress
            };
        }

        obj.Customer = Customer;
        let Physician = {
            givenName: orderData.physiciangivenName,
            familyName: orderData.physicianfamilyName,
            email: orderData.physicianemail,
            telephone: orderData.physiciantelephone,
            faxNumber: orderData.physicianfaxNumber
        };
        obj.Physician = Physician;
        console.log(obj);
        // saveOrder(obj)
        API.Orders.update({ data: obj }).then((response, err) => {
            console.log(response);
            if (response) {
                this.setState({ Order: response, loading: false });
            }

        })
            .catch(error => {
                console.log(error);
                //       this.setState({error: error.message})
            });
    };

    keyPress = e => {
        if (e.keyCode === 13) {
            console.log("value", e.target.value);
            this.loadData(e.target.value);
        }
    };

    render() {
        const { Order } = this.state
        const { Customer, OrderNotes, OrderLogs, Documents, OrderStatus, Physician } = Order
        let Empty = Object.keys(Order).length === 0 && Order.constructor === Object;


        return (
            <Formik
                enableReinitialize={true}
                initialValues={{
                    trackingkey: !Empty && Order.key ? Order.key : "",
                    givenName: !Empty && Customer ? Customer.givenName : "",
                    email: !Empty && Customer ? Customer.email : "",
                    familyName: !Empty && Customer ? Customer.familyName : "",
                    streetAddress:
                        !Empty && Customer && Customer.PostalAddress ? Customer.PostalAddress.streetAddress : "",
                    city: !Empty && Customer && Customer.PostalAddress ? Customer.PostalAddress.addressLocality : "",
                    state:
                        !Empty && Customer && Customer.PostalAddress
                            ? Customer.PostalAddress.addressRegion
                            : {
                                value: "",
                                label: ""
                            },
                    postalCode: !Empty && Customer && Customer.PostalAddress ? Customer.PostalAddress.postalCode : "",
                    telephone: !Empty && Customer ? Customer.telephone : "",
                    fax: !Empty && Customer ? Customer.faxNumber : "",
                    modality:
                        !Empty && Order.Modality
                            ? Order.Modality
                            : {
                                value: "",
                                label: ""
                            },
                    vendor:
                        !Empty && Order.Vendor
                            ? Order.Vendor
                            : {
                                value: "",
                                label: ""
                            },
                    notifyByMail: !Empty && Customer ? Customer.notifyByMail : false,
                    notifyByEmail: !Empty && Customer ? Customer.notifyByEmail : false,
                    notifyByFax: !Empty && Customer ? Customer.notifyByFax : false,
                    Studies: !Empty ? Order.Studies : [],
                    OrderNotes: !Empty ? OrderNotes : [],
                    OrderLogs: !Empty ? OrderLogs : [],
                    Documents: !Empty ? Documents : [],
                    orderStauesid: !Empty && OrderStatus ? OrderStatus.id : 0,
                    issued: !Empty && OrderStatus ? OrderStatus.issued : false,
                    received: !Empty && OrderStatus ? OrderStatus.received : false,
                    pending: !Empty && OrderStatus ? OrderStatus.pending : false,
                    read: !Empty && OrderStatus ? OrderStatus.read : false,
                    reportGenerated: !Empty && OrderStatus ? OrderStatus.reportGenerated : false,
                    inPlainEnglishGenerated: !Empty && OrderStatus ? OrderStatus.inPlainEnglishGenerated : false,
                    qualityChecked: !Empty && OrderStatus ? OrderStatus.qualityChecked : false,
                    physicianNotified: !Empty && OrderStatus ? OrderStatus.physicianNotified : false,
                    sent: !Empty && OrderStatus ? OrderStatus.sent : false,
                    physicianemail: !Empty && Physician ? Physician.email : "",
                    physiciangivenName: !Empty && Physician ? Physician.givenName : "",
                    physicianfamilyName: !Empty && Physician ? Physician.familyName : "",
                    physiciantelephone: !Empty && Physician ? Physician.telephone : "",
                    physicianfaxNumber: !Empty && Physician ? Physician.faxNumber : "",
                    AddStudy: false,
                    AddNote: false,
                    AddNotification: false,
                    SendReport: false,
                    Reset: false,
                    checkIssued: false,
                    checkRecived: false,
                    checkPending: false,
                    checkRead: false,
                    checkReportGenerated: false,
                    checkInplainEnglish: false,
                    checkQualitychecked: false,
                    checkPhysicianNotified: false,
                    checkSent: false
                }}

                render={({ values, handleChange, setFieldValue }) => (
                    <div className="form-sec">
                        {this.state.loading ? (
                            <div
                                style={{
                                    display: "flex",
                                    justifyContent: "center",
                                    height: "100vh",
                                    alignItems: "center"
                                }}
                            >
                                <Loader type="ThreeDots" color="black" height="100" width="100" />
                            </div>
                        ) : (
                                <div className="container">
                                    <div className="row">
                                        {/* {JSON.stringify(values)} */}
                                        {this.state.error ? <div className="errors">{this.state.message}</div> : null}
                                        <div className="col-lg-10">
                                            <div className="form-left">
                                                <div className="row align-items-end">
                                                    <div className="col-md-4 pr-1">
                                                        <label>Enter Tracking key</label>
                                                        <InputMask
                                                            mask="****-****-****"
                                                            maskChar={null}
                                                            placeholder="xxxx-xxxx-xxxx"
                                                            type="text"
                                                            name="trackingkey"
                                                            onBlur={e => {
                                                                if (e.target.value.length === 14) {
                                                                    this.loadData(e.target.value);
                                                                }
                                                            }}
                                                            className="form-control track"
                                                            onChange={handleChange}
                                                            onKeyDown={this.keyPress}
                                                            value={values.trackingkey.toUpperCase()}
                                                        />
                                                    </div>
                                                    <div className="col-md-2 px-1">
                                                        <label htmlFor="study">Type of Study</label>
                                                        <Select
                                                            placeholder="Type Of Study"
                                                            className="track"
                                                            isDisabled={!this.state.loaded}
                                                            value={values.modality}
                                                            name="modality"
                                                            options={this.state.modalities}
                                                            onChange={e => {
                                                                console.log(e);
                                                                setFieldValue("modality", e);
                                                            }}
                                                        />
                                                    </div>
                                                    <div className="col-md-2 px-1">
                                                        <label htmlFor="vendor">Vendor</label>
                                                        <Select
                                                            placeholder="Vendors"
                                                            className="track"
                                                            value={values.vendor}
                                                            isDisabled={values.issued || !values.loaded}
                                                            name="modality"
                                                            options={this.state.Vendors}
                                                            onChange={e => {
                                                                console.log(e);
                                                                setFieldValue("vendor", e);
                                                            }}
                                                        />
                                                    </div>
                                                </div>
                                                <div className="customer-info">
                                                    <span>Customers Information</span>
                                                    <form className="customer-sec">
                                                        <div className="row align-items-end">
                                                            <div className="col-md-4 pr-1">
                                                                <label htmlFor="firstName">FIRST NAME</label>

                                                                <input
                                                                    type="text"
                                                                    name="givenName"
                                                                    className="form-control"
                                                                    disabled={!this.state.loaded}
                                                                    value={values.givenName}
                                                                    onChange={handleChange}
                                                                    placeholder=""
                                                                />
                                                            </div>
                                                            <div className="col-md-4 px-1">
                                                                <label htmlFor="lastName">LAST NAME</label>
                                                                <input
                                                                    type="text"
                                                                    name="familyName"
                                                                    disabled={!this.state.loaded}
                                                                    className="form-control"
                                                                    value={values.familyName}
                                                                    onChange={handleChange}
                                                                    placeholder=""
                                                                />
                                                            </div>
                                                            <div className="col-m-3 px-0">
                                                                <div className="notification">
                                                                    <div className="form-check form-check-inline">
                                                                        <input
                                                                            className="form-check-input"
                                                                            type="checkbox"
                                                                            disabled={!this.state.loaded}
                                                                            name="notifyByMail"
                                                                            checked={values.notifyByMail}
                                                                            value={values.notifyByMail}
                                                                            id="inlineRadio1"
                                                                            onChange={handleChange}
                                                                        // value="option1"
                                                                        />
                                                                        <label className="form-check-label" htmlFor="inlinecheck1">
                                                                            MAIL
                                                                    </label>
                                                                    </div>
                                                                    <div className="form-check form-check-inline">
                                                                        <input
                                                                            className="form-check-input"
                                                                            type="checkbox"
                                                                            disabled={!this.state.loaded}
                                                                            id="inlineRadio2"
                                                                            name="notifyByEmail"
                                                                            value={values.notifyByEmail}
                                                                            checked={values.notifyByEmail}
                                                                            onChange={handleChange}
                                                                        />
                                                                        <label className="form-check-label" htmlFor="inlinecheck2">
                                                                            EMAIL
                                                                    </label>
                                                                    </div>
                                                                    <div className="form-check form-check-inline">
                                                                        <input
                                                                            className="form-check-input"
                                                                            type="checkbox"
                                                                            disabled={!this.state.loaded}
                                                                            name="notifyByFax"
                                                                            checked={values.notifyByFax}
                                                                            value={values.notifyByFax}
                                                                            onChange={handleChange}
                                                                            id="inlineRadio3"
                                                                            value="option3"
                                                                        />
                                                                        <label className="form-check-label" htmlFor="inlinecheck3">
                                                                            FAX
                                                                    </label>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="row">
                                                            <div className="col-md-4 pr-1">
                                                                <label htmlFor="emial">EMAIL</label>
                                                                <input
                                                                    type="text"
                                                                    className="form-control"
                                                                    disabled={!this.state.loaded}
                                                                    name="email"
                                                                    value={values.email}
                                                                    onChange={handleChange}
                                                                    id="email"
                                                                    placeholder=""
                                                                />
                                                            </div>
                                                            <div className="col-md-4 px-1">
                                                                <label htmlFor="phone">PHONE</label>
                                                                <InputMask
                                                                    mask="***-***-****"
                                                                    maskChar={null}
                                                                    disabled={!this.state.loaded}
                                                                    placeholder="xxx-xxx-xxxx"
                                                                    type="text"
                                                                    className="form-control"
                                                                    name="telephone"
                                                                    onChange={handleChange}
                                                                    value={values.telephone}
                                                                />
                                                            </div>
                                                            <div className="col-md-4 pl-0">
                                                                <label htmlFor="fax">FAX</label>

                                                                <InputMask
                                                                    mask="***-***-****"
                                                                    maskChar={null}
                                                                    disabled={!this.state.loaded}
                                                                    placeholder="xxx-xxx-xxxx"
                                                                    className="form-control"
                                                                    name="fax"
                                                                    onChange={handleChange}
                                                                    value={values.fax}
                                                                />
                                                            </div>
                                                        </div>
                                                        <div className="row">
                                                            <div className="col-md-6 pr-0">
                                                                <label htmlFor="addressST">STREET ADDRESS</label>
                                                                <input
                                                                    disabled={!this.state.loaded}
                                                                    type="text"
                                                                    className="form-control"
                                                                    id="addressST"
                                                                    name="streetAddress"
                                                                    value={values.streetAddress}
                                                                    onChange={handleChange}
                                                                    placeholder=""
                                                                />
                                                            </div>
                                                            <div className="col-md-6">
                                                                <div className="row">
                                                                    <div className="col-md-4 px-1">
                                                                        <label htmlFor="city">CITY</label>
                                                                        <input
                                                                            disabled={!this.state.loaded}
                                                                            type="text"
                                                                            className="form-control"
                                                                            id="city"
                                                                            name="city"
                                                                            value={values.city}
                                                                            onChange={handleChange}
                                                                            placeholder=""
                                                                        />
                                                                    </div>
                                                                    <div className="col-md-3 px-0">
                                                                        <label htmlFor="state">STATE</label>
                                                                        <Select
                                                                            isDisabled={!this.state.loaded}
                                                                            placeholder="States"
                                                                            className="track"
                                                                            value={values.state}
                                                                            name="state"
                                                                            options={this.state.States}
                                                                            onChange={e => {
                                                                                console.log(e);
                                                                                setFieldValue("state", e);
                                                                            }}
                                                                        />
                                                                    </div>
                                                                    <div className="col-md-5 pl-2">
                                                                        <label htmlFor="psotal">POSTAL</label>
                                                                        <InputMask
                                                                            disabled={!this.state.loaded}
                                                                            mask="*****-****"
                                                                            maskChar={null}
                                                                            placeholder="xxxxx-xxxx"
                                                                            type="text"
                                                                            className="form-control"
                                                                            id="postal"
                                                                            name="postalCode"
                                                                            value={values.postalCode}
                                                                            onChange={handleChange}
                                                                        />
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </form>
                                                </div>
                                                <div className="physicians-info">
                                                    <span>Custiomer Physicians Information</span>
                                                    <div className="physicians-sec">
                                                        <div className="row">
                                                            <div className="col-md-4 pr-0">
                                                                <label htmlFor="firstName">FIRST NAME</label>
                                                                <input
                                                                    disabled={!this.state.loaded}
                                                                    type="text"
                                                                    className="form-control"
                                                                    id="firstName"
                                                                    name="physiciangivenName"
                                                                    value={values.physiciangivenName}
                                                                    onChange={handleChange}
                                                                    placeholder=""
                                                                />
                                                            </div>
                                                            <div className="col-md-4 pl-1">
                                                                <label htmlFor="lastName">LAST NAME</label>
                                                                <input
                                                                    disabled={!this.state.loaded}
                                                                    type="text"
                                                                    className="form-control"
                                                                    id="lastName"
                                                                    name="physicianfamilyName"
                                                                    value={values.physicianfamilyName}
                                                                    onChange={handleChange}
                                                                    placeholder=""
                                                                />
                                                            </div>
                                                        </div>
                                                        <div className="row">
                                                            <div className="col-md-4 pr-1">
                                                                <label htmlFor="emial">EMAIL</label>
                                                                <input
                                                                    disabled={!this.state.loaded}
                                                                    type="text"
                                                                    className="form-control"
                                                                    id="email"
                                                                    name="physicianemail"
                                                                    value={values.physicianemail}
                                                                    onChange={handleChange}
                                                                    placeholder=""
                                                                />
                                                            </div>
                                                            <div className="col-md-4 px-0">
                                                                <label htmlFor="phone">PHONE</label>
                                                                <InputMask
                                                                    disabled={!this.state.loaded}
                                                                    mask="***-***-****"
                                                                    maskChar={null}
                                                                    placeholder="xxx-xxx-xxxx"
                                                                    type="text"
                                                                    className="form-control"
                                                                    name="physiciantelephone"
                                                                    onChange={handleChange}
                                                                    value={values.physiciantelephone}
                                                                />
                                                            </div>
                                                            <div className="col-md-4 pl-1">
                                                                <label htmlFor="fax">FAX</label>
                                                                <InputMask
                                                                    mask="***-***-****"
                                                                    maskChar={null}
                                                                    disabled={!this.state.loaded}
                                                                    placeholder="xxx-xxx-xxxx"
                                                                    type="text"
                                                                    className="form-control"
                                                                    name="physicianfaxNumber"
                                                                    onChange={handleChange}
                                                                    value={values.physicianfaxNumber}
                                                                />
                                                            </div>
                                                        </div>
                                                        <div className="row phy-btns-row">
                                                            {values.Reset ? (
                                                                <ORDERSTATUS
                                                                    show={values.Reset}
                                                                    key="Reset"
                                                                    handleClose={() => setFieldValue("Reset", false)}
                                                                    onSubmit={() => {
                                                                        this.setState({
                                                                            Order: {}
                                                                        });
                                                                        setFieldValue("Reset", false);
                                                                    }}
                                                                />
                                                            ) : null}
                                                            <div className="col-md-2 px-1">
                                                                <button
                                                                    onClick={() => setFieldValue("Reset", true)}
                                                                    type="button"
                                                                    disabled={!this.state.loaded}
                                                                    className="btn btn-primary custom_btn"
                                                                >
                                                                    Reset
                                                            </button>
                                                            </div>
                                                            <div className="col-md-2 pl-0">
                                                                <button
                                                                    disabled={values.trackingkey}
                                                                    type="button"
                                                                    disabled={!this.state.loaded}
                                                                    className="btn btn-primary custom_btn"
                                                                    onClick={() => this.SendOrder(values)}
                                                                >
                                                                    SAVE
                                                            </button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="studies-sec">
                                                    <span>Studies</span>
                                                    <div className="studies">
                                                        <div className="row">
                                                            <div className="col-md-10">
                                                                <table className="studies-table access_number">
                                                                    <thead>
                                                                        <tr>
                                                                            <th className="st_heaing">ID</th>
                                                                            <th className="st_heaing">ACCESSION NUMBER</th>
                                                                            <th className="st_heaing">TYPE</th>
                                                                            <th className="st_heaing">SENT TO EXA</th>
                                                                            <th className="st_heaing">STATUS</th>
                                                                        </tr>
                                                                    </thead>
                                                                    <tbody>
                                                                        {values.Studies.length > 0 ? (
                                                                            values.Studies.map((study, i) => {
                                                                                return (
                                                                                    <tr key={i}>
                                                                                        <td className="st-data">{study.id}</td>
                                                                                        <td className="st-data">{study.accessionNumber}</td>
                                                                                        <td className="st-data">{study.type}</td>
                                                                                        <td className="st-data">{study.sentToExa.toString()}</td>
                                                                                        <td className="st-data">{study.status}</td>
                                                                                    </tr>
                                                                                );
                                                                            })
                                                                        ) : (
                                                                                <tr>
                                                                                    <td colSpan="8" className="text-center">
                                                                                        No Record Found
                                                                        </td>
                                                                                </tr>
                                                                            )}
                                                                    </tbody>
                                                                </table>
                                                            </div>
                                                            <div className="col-md-2 ml-auto d-flex align-items-end justify-content-end">
                                                                <button
                                                                    type="button"
                                                                    className="btn btn-primary custom_btn"
                                                                    disabled={!this.state.loaded}
                                                                    onClick={e => {
                                                                        console.log(e);
                                                                        setFieldValue("AddStudy", !values.AddStudy);
                                                                    }}
                                                                >
                                                                    Add Study
                                                            </button>
                                                                {values.AddStudy ? (
                                                                    <ADDSTUDY
                                                                        show={values.AddStudy}
                                                                        orderKey={values.Studies.length > 0 ? values.Studies[values.Studies.length - 1].accessionNumber : values.trackingkey}
                                                                        handleClose={() => setFieldValue("AddStudy", false)}
                                                                        onUpload={(orderid, notes, content) => this.UploadStudy(Order.id, notes, content)}
                                                                    />
                                                                ) : null}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div className="studies-sec">
                                                    <span>Order Log</span>
                                                    <div className="studies">
                                                        <div className="row">
                                                            <div className="col-md-10">
                                                                <table className="studies-table">
                                                                    <thead>
                                                                        <tr>
                                                                            <th className="st_heaing">ID</th>
                                                                            <th className="st_heaing">CREATED BY</th>
                                                                            <th className="st_heaing">RECEIVED BY</th>
                                                                            <th className="st_heaing">TYPE</th>
                                                                            <th className="st_heaing">DATA</th>
                                                                            <th className="st_heaing">COMPLETED AT</th>
                                                                            <th className="st_heaing">DOCS SENT</th>
                                                                            <th className="st_heaing">NOTE</th>
                                                                        </tr>
                                                                    </thead>
                                                                    <tbody>
                                                                        {values.OrderLogs.length > 0 ? (
                                                                            values.OrderLogs.map((log, i) => {
                                                                                return (
                                                                                    <tr key={i}>
                                                                                        <td className="st-data">{log.id}</td>
                                                                                        <td className="st-data">
                                                                                            {log.CreatedBy.givenName} {log.CreatedBy.familyName}
                                                                                        </td>
                                                                                        <td className="st-data">{log.receivedBy}</td>
                                                                                        <td className="st-data">{log.type}</td>
                                                                                        <td className="st-data">{log.data}</td>
                                                                                        <td className="st-data">{new Date(log.createdAt).toDateString()} {new Date(log.createdAt).toLocaleTimeString()}</td>
                                                                                        <td className="st-data">{log.documentsSent.toString()}</td>
                                                                                        <td className="st-data">{log.note}</td>
                                                                                    </tr>
                                                                                );
                                                                            })
                                                                        ) : (
                                                                                <tr>
                                                                                    <td colSpan="8" className="text-center">
                                                                                        No Record Found
                                                                        </td>
                                                                                </tr>
                                                                            )}
                                                                    </tbody>
                                                                </table>
                                                            </div>
                                                            <div className="col-md-2 ml-auto d-flex align-items-end justify-content-end">
                                                                <button
                                                                    type="button"
                                                                    className="btn btn-primary custom_btn"
                                                                    disabled={!this.state.loaded}
                                                                    onClick={e => {
                                                                        setFieldValue("AddNotification", !values.AddNotification);
                                                                    }}
                                                                >
                                                                    Add Entry
                                                            </button>

                                                                {values.AddNotification ? (
                                                                    <ADDNOTIFICATION
                                                                        show={values.AddNotification}
                                                                        data={values}
                                                                        handleSubmit={object => {
                                                                            setFieldValue("AddNotification", false);
                                                                            this.SubmitOrderLogs(object);
                                                                        }}
                                                                        handleClose={() => setFieldValue("AddNotification", false)}
                                                                    />
                                                                ) : null}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div className="report-sec">
                                                    <span>Documents</span>
                                                    <div className="report-text">
                                                        {values.Documents.length > 0 ? (
                                                            values.Documents.map((log, i) => {
                                                                return (
                                                                    <div colSpan="4" key={i}>
                                                                        <iframe
                                                                            width="75%"
                                                                            height="500"
                                                                            src={`${log.pdfUrl}?page=hsn#toolbar=0`}
                                                                            frameBorder="0"
                                                                        />

                                                                    </div>
                                                                );
                                                            })
                                                        ) : (
                                                                <div colSpan="4" className="text-center">
                                                                    No Record Found
                                                        </div>
                                                            )}

                                                        <button
                                                            type="button"
                                                            className="btn btn-primary custom_btn report-btn"
                                                            disabled={!values.qualityChecked || !this.state.loaded}
                                                            onClick={e => {
                                                                setFieldValue("AddNotification", !values.AddNotification);
                                                                // handleSubmit(e);
                                                            }}
                                                        >
                                                            Send Document
                                                    </button>

                                                        {values.AddNotification ? (
                                                            <ADDNOTIFICATION
                                                                show={values.AddNotification}
                                                                data={values}
                                                                handleSubmit={object => {
                                                                    setFieldValue("AddNotification", false);
                                                                    this.SubmitOrderLogs(object);
                                                                    //   this.changeOrderStatus(
                                                                    //     "pending",
                                                                    //     values
                                                                    // )
                                                                }}
                                                                handleClose={() => setFieldValue("AddNotification", false)}
                                                            />
                                                        ) : null}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-lg-2">
                                            <div className="from-right-sec">
                                                <span>Order Status</span>
                                                <div className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        name="issued"
                                                        disabled={values.issued || !this.state.loaded}
                                                        checked={values.issued}
                                                        onClick={() => console.log(values)}
                                                        onChange={e => {
                                                            setFieldValue("checkIssued", true);
                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="inlineCheckbox1">
                                                        Issued
                                                </label>
                                                    {values.checkIssued ? (
                                                        <ORDERSTATUS
                                                            show={values.checkIssued}
                                                            handleClose={() => setFieldValue("checkIssued", false)}
                                                            onSubmit={() => {
                                                                this.changeOrderStatus("issued", values);
                                                                setFieldValue("checkIssued", false);
                                                            }}
                                                        />
                                                    ) : null}

                                                </div>
                                                <div className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="checkbox"

                                                        checked={values.received}
                                                        disabled={
                                                            !values.issued ||
                                                            values.received ||
                                                            values.givenName === "" ||
                                                            values.familyName === "" ||
                                                            values.email === "" ||
                                                            values.telephone === "" ||
                                                            values.faxNumber === "" ||
                                                            (values.notifyByMail === false && values.notifyByEmail === false && values.notifyByFax === false) ||
                                                            values.streetAddress === "" ||
                                                            values.city === "" ||
                                                            values.state.label === ""
                                                        }
                                                        name="received"
                                                        onChange={e => {
                                                            setFieldValue("checkRecived", true);
                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="inlineCheckbox2">
                                                        Recived
                                                </label>
                                                    {values.checkRecived ? (
                                                        <ORDERSTATUS
                                                            show={values.checkRecived}
                                                            handleClose={() => setFieldValue("checkRecived", false)}
                                                            onSubmit={() => {
                                                                this.changeOrderStatus("received", values);
                                                                setFieldValue("checkRecived", false);
                                                            }}
                                                        />
                                                    ) : null}
                                                </div>
                                                <div className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        name="pending"
                                                        disabled={!values.received || values.pending}
                                                        checked={values.pending}
                                                        onChange={e => {
                                                            setFieldValue("checkPending", true)
                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="inlineCheckbox3">
                                                        Pending
                                                </label>
                                                    {values.checkPending ? (
                                                        <ORDERSTATUS
                                                            show={values.checkPending}
                                                            handleClose={() => setFieldValue("checkPending", false)}
                                                            onSubmit={() => {
                                                                this.changeOrderStatus("pending", values);
                                                                setFieldValue("checkPending", false);
                                                            }}
                                                        />
                                                    ) : null}
                                                </div>
                                                <div className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        disabled={!values.pending || values.read}
                                                        name="read"
                                                        checked={values.read}
                                                        onChange={e => {
                                                            setFieldValue("checkRead", true)
                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="inlineCheckbox4">
                                                        Read
                                                </label>
                                                    {values.checkRead ? (
                                                        <ORDERSTATUS
                                                            show={values.checkRead}
                                                            key="read"
                                                            handleClose={() => setFieldValue("checkRead", false)}
                                                            onSubmit={(e) => {
                                                                setFieldValue("read", true)
                                                                this.changeOrderStatus("read", values);
                                                                setFieldValue("checkRead", false);
                                                            }}
                                                        />
                                                    ) : null}
                                                </div>
                                                <div className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        disabled={!values.read || values.reportGenerated}
                                                        name="reportGenerated"
                                                        value={values.reportGenerated}
                                                        checked={values.reportGenerated}
                                                        onChange={e => {
                                                            setFieldValue("checkReportGenerated", true);

                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="inlineCheckbox5">
                                                        Report Generated
                                                </label>
                                                    {values.checkReportGenerated ? (
                                                        <ORDERSTATUS
                                                            show={values.checkReportGenerated}
                                                            handleClose={() => setFieldValue("checkReportGenerated", false)}
                                                            onSubmit={() => {
                                                                this.changeOrderStatus("reportGenerated", values);
                                                                setFieldValue("checkReportGenerated", false);
                                                            }}
                                                        />
                                                    ) : null}
                                                </div>
                                                <div className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        disabled={!values.reportGenerated || values.inPlainEnglishGenerated}
                                                        name="inPlainEnglishGenerated"
                                                        value={values.inPlainEnglishGenerated}
                                                        checked={values.inPlainEnglishGenerated}
                                                        onChange={e => {
                                                            setFieldValue("checkInplainEnglish", true);
                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="inlineCheckbox5">
                                                        In Plain English Generated
                                                </label>
                                                    {values.checkInplainEnglish ? (
                                                        <ORDERSTATUS
                                                            show={values.checkInplainEnglish}
                                                            handleClose={() => setFieldValue("checkInplainEnglish", false)}
                                                            onSubmit={() => {
                                                                this.changeOrderStatus("inPlainEnglishGenerated", values);
                                                                setFieldValue("checkInplainEnglish", false);
                                                            }}
                                                        />
                                                    ) : null}
                                                </div>
                                                <div className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        disabled={!values.read || values.qualityChecked}
                                                        name="qualityChecked"
                                                        value={values.qualityChecked}
                                                        checked={values.qualityChecked}
                                                        onChange={e => {
                                                            setFieldValue("checkQualitychecked", true);

                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="inlineCheckbox5">
                                                        Quality Checked
                                                </label>
                                                    {values.checkQualitychecked ? (
                                                        <ORDERSTATUS
                                                            show={values.checkQualitychecked}
                                                            handleClose={() => setFieldValue("checkQualitychecked", false)}
                                                            onSubmit={() => {
                                                                this.changeOrderStatus("qualityChecked", values);
                                                                setFieldValue("checkQualitychecked", false);
                                                            }}
                                                        />
                                                    ) : null}
                                                </div>
                                                <div className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        disabled={!values.qualityChecked || values.physicianNotified}
                                                        name="physicianNotified"
                                                        checked={values.physicianNotified}
                                                        value={values.physicianNotified}
                                                        onChange={e => {
                                                            setFieldValue("checkPhysicianNotified", true);
                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="inlineCheckbox6">
                                                        Physician Notified
                                                </label>
                                                    {values.checkPhysicianNotified ? (
                                                        <ORDERSTATUS
                                                            show={values.checkPhysicianNotified}
                                                            handleClose={() => setFieldValue("checkPhysicianNotified", false)}
                                                            onSubmit={() => {
                                                                this.changeOrderStatus("physicianNotified", values);
                                                                setFieldValue("checkPhysicianNotified", false);
                                                            }}
                                                        />
                                                    ) : null}
                                                </div>
                                                <div className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        //  id="inlineCheckbox7"
                                                        disabled={!values.physicianNotified || values.sent}
                                                        name="sent"
                                                        checked={values.sent}
                                                        onChange={e => {
                                                            setFieldValue("checkSent", true);
                                                        }}
                                                    />
                                                    <label className="form-check-label" htmlFor="inlineCheckbox7">
                                                        Sent
                                                </label>
                                                    {values.checkSent ? (
                                                        <ORDERSTATUS
                                                            show={values.checkSent}
                                                            handleClose={() => setFieldValue("checkSent", false)}
                                                            onSubmit={() => {
                                                                this.changeOrderStatus("sent", values);
                                                                setFieldValue("checkSent", false);
                                                            }}
                                                        />
                                                    ) : null}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )}
                    </div>
                )}
            />
        );
    }
}

export default OrderEntry;
