import React, { Component } from "react";
import {
  Grid,
  Col,
  FormGroup,
  ControlLabel,
  FormControl
} from "react-bootstrap";
import { withFormik, Form } from 'formik';
import * as Yup from 'yup';
import { API } from "../../api/index";
import { auth } from './Auth';
import { Redirect } from "react-router-dom";
import './login.css'
import Loader from "react-loader-spinner";

class LoginPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: '',
      email: '',
      password: '',
      isSubmitting: false
    };
  }

  OnSubmit = () => {
    const { email, password } = this.state
    this.setState({ error: '', isSubmitting: true })
    auth.login(email, password).then((response, err) => {
      console.log(response);
      if (response === true) {
        this.setState({ isSubmitting: false });
        return <Redirect to='/admin' />
      }

    })
      .catch(error => {
        console.log(error);
        //       this.setState({error: error.message})
      });
  }


  handleChange = (e) => {
    this.setState({ [e.target.name]: e.target.value })
  }
  render() {
    const { email, password, error, isSubmitting } = this.state;

    // const { requestIsRunning, error,redirectToReferrer } = this.props.loginPage; //error = server side error
    if (auth.loggedIn()) {
      return <Redirect to="/admin" />
    }
    return isSubmitting ? (
      <div className="loader" >
        <Loader type="ThreeDots" color="black" height="100" width="100" />
      </div >
    ) : (
        <div className="container">
          <div className="form" id="front-form">
            <div className="logo">
              <img src="../images/site-logo.png" alt="site logo" />
            </div>
            <Grid>
              <Col>
                <Form >
                  <div>
                    {<small className="text">{error && error}</small>}

                    <FormGroup
                    >
                      <div className="field">

                        <label>Email address</label>
                        <FormControl placeholder="Enter email"
                          name="email"

                          value={email}
                          onChange={this.handleChange} />
                      </div>
                    </FormGroup>

                    <FormGroup
                    >
                      <div className="field">

                        <label>Password</label>
                        <FormControl
                          placeholder="Password"
                          name="password"
                          type="password"
                          value={password}
                          onChange={this.handleChange}

                        />
                      </div>
                    </FormGroup>

                  </div>
                  <div className="form-note">
                    <button type="submit" disabled={email === '' || password === ''} onClick={this.OnSubmit} className="submit-btn">Login</button>
                  </div>
                  {/* <Button type="submit" disabled={isSubmitting} bsStyle="info" fill wd>
                Login
           </Button> */}


                </Form>
              </Col>
            </Grid>
          </div>
        </div>

      )


  }
}

const FormikApp = withFormik({
  mapPropsToValues(props) {

    return {
      email: '',
      password: ''
    }
  },
  handleSubmit({ email, password }, { props, resetForm, setErrors, setSubmitting }) {
    setSubmitting(true)
    auth.login(email, password).then(res => {
      setSubmitting(false)
    }).catch(err => {
      console.log(`TODO: handle error: `, err)
      setErrors({ error: "Request Cannot be processed" })

      // setErrors({ error:err.response?err.response.data:err.message })
      setSubmitting(false)
    })
  },
  validationSchema: Yup.object().shape({
    email: Yup.string().email().required(),
    password: Yup.string().required()
  })
})(LoginPage)



export default FormikApp;
