import {
    API
} from "../../api/index";
const localStorage = global.window.localStorage;
const baseURL = `http://secondlook-life-1969196284.us-east-1.elb.amazonaws.com`;

export const auth = {

    login(email, password) {

        if (auth.loggedIn()) {
            return Promise.resolve(true);
        }
        let data = {
            email,
            password
        }

        // return fetch(`${baseURL}/api/tracking-keys/${trackerKey}`, options)
        return API.Sessions.create({ data }).then((response, err) => { //store user details in local storage
            console.log(response)
            localStorage.token = response.token;

            return Promise.resolve(true);
        }).catch(err => {
            console.log(err)
            return Promise.reject(false);
        })
    },

    loggedIn() {
        return !!localStorage.token;
    },

    logout(history) {

        localStorage.clear();
        history.push('/login');

    },

};