
import React, { Component } from 'react';
import InputMask from 'react-input-mask';

import { API } from "../../../../api/index";

class AboutTracker extends Component {

    constructor(props) {
        super(props);
        this.state = {
            tracker: null,
            trackerKey: '',
            error: null,
            success: null
        }

        this.handleTrackerKeyChange = this.handleTrackerKeyChange.bind(this);
        this.loadTrackerByKey = this.loadTrackerByKey.bind(this);
    }


    handleTrackerKeyChange(evt) {
        this.setState({ trackerKey: evt.target.value.toUpperCase() })
    }

    loadTrackerByKey(e) {
        e.preventDefault();

        this.setState({ error: null, tracker: null })

        const key = this.state.trackerKey && this.state.trackerKey.toUpperCase();
        //  if (!trackerKey) return;
        console.log(key)
        if (key.length === 14) {
            API.Orders.get({ id: key })
                .then((res, err) => {
                    console.log(res)
                    if (!res) {
                        this.setState({ error: "Invalid Order Key" })
                    } else {
                        let response = res.OrderStatus

                        this.setState({ tracker: response });
                        if (response && response.received && response.pending && response.reportGenerated && response.qualityChecked && response.sent) {
                            this.setState({ success: "Your SecondLook.Life Report is on its way via secure email, fax or mail." });
                        }
                        else if (response && response.received && response.pending && response.reportGenerated && response.qualityChecked) {
                            this.setState({ success: "Our quality control check is completed, it's ready to ship!" });

                        }
                        else if (response && response.received && response.pending && response.reportGenerated) {
                            this.setState({ success: "Your  SecondLook.Life Report is done!  Your report, packed with information, will be on its way shortly!" });

                        }
                        else if (response && response.received && response.pending) {
                            this.setState({ success: "We've got your images and our radiologists are in the process of reviewing them." });

                        }
                        else if (response && response.received) {
                            this.setState({ success: "Success! We've received your CD! The process has started, check back each step of the way." });

                        }
                        else {
                            this.setState({ success: null });

                        }
                    }
                })
                .catch(error => {
                    console.log(error)
                    this.setState({ error: error.message })
                });
        }
        else {
            this.setState({ error: "Invalid format, Please enter all 12 digits of your Tracking ID" })
        }
    }


    render() {
        return (<div>
            <section id="tracker" className="life_Tracker_sec">
                <div className="container">
                    <div className="row">
                        <div className="col-sm-12">
                            <div className="life-tracker-sec-text">
                                <h2>SecondLook.Life Tracker</h2>
                                <p>Track your SecondLook.Life Report every step of the way.</p>
                            </div>
                            <div className="tracker_list">
                                <ul>
                                    <li className={this.state.tracker && this.state.tracker.received ? "report-received" : ""}><img
                                        src="images/tracker-step1_disabled@3x.png" width="220" alt="tracker-step1_disabled@3x.png" /></li>
                                    <li className={this.state.tracker && this.state.tracker.pending ? "report-pending" : ""}><img
                                        src="images/tracker-step2_disabled@3x.png" width="220" alt="tracker-step1_disabled@3x.png" /></li>
                                    <li className={this.state.tracker && this.state.tracker.reportGenerated ? "report-done" : ""}><img
                                        src="images/tracker-step3_disabled@3x.png" width="220" alt="tracker-step1_disabled@3x.png" /></li>
                                    <li className={this.state.tracker && this.state.tracker.qualityChecked ? "report-check" : ""}><img
                                        src="images/tracker-step4_disabled.png" width="220" alt="tracker-step1_disabled@3x.png" /></li>
                                    <li className={this.state.tracker && this.state.tracker.sent ? "report-sent" : ""}><img
                                        src="images/tracker-step5_disabled@3x.png" width="220" alt="tracker-step1_disabled@3x.png" /></li>
                                </ul>

                            </div>
                            {this.state.tracker && this.state.success ? <div className="package-here"><h3>{this.state.success}</h3></div> : null}

                            <div className="track-order">
                                <h3>Track your Order</h3>
                                <form action="#" method="get">
                                    {/* <input type="text" name="email" placeholder="xxxx-xxxx-xxxx" /> */}
                                    <InputMask mask="****-****-****" maskChar={null}
                                        placeholder="xxxx-xxxx-xxxx" type="text"
                                        onChange={this.handleTrackerKeyChange}
                                        value={this.state.trackerKey}
                                    />
                                    <input type="submit" name="submit" value="Submit" onClick={this.loadTrackerByKey} />
                                </form>
                                {this.state.error && <h2 style={{ color: 'Red' }}> {this.state.error}</h2>}
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>);
    }
}

export default AboutTracker;
