const GET = 'GET'
const POST = 'POST'
const PUT = 'PUT'
const DELETE = 'DELETE'

export class Api {
    constructor({ token = '' }) {
        this._token = token
        this._endpoints = [
            {name: 'OrderLog', url: '/api/order-logs'},
            {name: 'Orders', url: '/api/orders'},
            {name: 'OrderStatuses', url: '/api/order-statuses'},
            {name: 'Regions', url: '/api/regions'},
            {name: 'Vendors', url: '/api/vendors'},
            {name: 'PostalAddresses', url: '/api/postal-addresses'},
            {name: 'Persons', url: '/api/persons'},
            {name: 'Modalities', url: '/api/modalities'},
            {name: 'Studies', url: '/api/studies', customFunctions: [{funcName: 'uploadStudy', func: this._uploadStudy}]},
            {name: 'Sessions', url: '/api/sessions', customFunctions: [{funcName: 'login', func: this._login}]},
        ]
        this._endpoints.forEach(endpoint => this.createEndpoint(endpoint))
    }

    _generateGetEndpoint({ name, url }) {
        this[name]['get'] = ({ id, offset = 0, limit = 20, all = false }) => {
            let query = (all) ? '' : `?offset=${offset}&limit=${limit}`
            if (id) {
                return fetch(`${url}/${id}${query}`, this.getOptions({})).then(res => res.json())
            }
            return fetch(`${url}${query}`, this.getOptions({ method: GET })).then(res => res.json())
        }
    }

    _generateCreateEndpoint({ name, url }) {
        this[name]['create'] = ({ data }) => {
            return fetch(`${url}`, this.getOptions({ method: POST, data })).then(res => res.json())
        }
    }
    _generateUpdateEndpoint({ name, url }) {
        this[name]['update'] = ({ data }) => {
            return fetch(`${url}`, this.getOptions({ method: PUT, data })).then(res => res.json())
        }
    }
    _generateDeleteEndpoint({ name, url }) {
        this[name]['delete'] = ({ id }) => {
            return fetch(`${url}/${id}`, this.getOptions({ method: DELETE })).then(res => res.json())
        }
    }
    createEndpoint({ name, url, methods = ['create', 'get', 'update', 'delete'], customFunctions = [] }) {
        methods.forEach(method => {
            this[name] = this[name] || {}
            switch (method) {
                case 'create': return this._generateCreateEndpoint({ name, url })
                case 'get': return this._generateGetEndpoint({ name, url })
                case 'update': return this._generateUpdateEndpoint({ name, url })
                case 'delete': return this._generateDeleteEndpoint({ name, url })
            }
        })
        customFunctions.forEach(({ funcName, func }) => this[name][funcName] = func)
    }

    getOptions({ method = 'GET', data, contentType }) {
        // default to application/json unless null
        contentType = contentType === undefined ? 'application/json' : contentType === null ? undefined : contentType
        let headers = {
            "Content-Type": contentType,
            "Authorization": `Bearer ${this.token}`
        }
        let body = (contentType === 'application/json') ? JSON.stringify(data) : data
        return { headers, method, body }
    }

    async login({ email, password }) {
        return fetch(`/api/login`, this.getOptions({ method: POST, data: { email, password } }))
            .then(res => res.json())
            .then(res => this.token = res.token)
    }


    async _uploadStudy(data) {
        console.log(data)
        let { orderId, notes, dicom }=data
        let form = new FormData();
        form.append("orderId", parseInt(orderId));
        form.append("notes", notes);
        form.append("dicom", dicom);
        console.log(form)
        return fetch(`/api/studies`, this.getOptions({ method: POST, data: form, contentType: null }))
            .then(res => res.json())
    }

    async _login({email, password}) {
        let form = new FormData();
        form.append("email", email);
        form.append("password", password);

        return fetch(`/api/sessions`, this.getOptions({method: POST, data: form, contentType: null}))
            .then(res => res.json())
            .then(({token}) => this.token = token)
    }

    get token() {
        return this._token
    }

    set token(value) {
        this._token = value
    }
}
